import styled from 'styled-components'
import {
  fontSize,
  space,
  width,
  maxWidth,
  minWidth,
  position,
  top,
  bottom,
  left,
  right,
  flex,
  alignItems,
  justifyContent,
  flexDirection,
  height,
  maxHeight,
  minHeight,
  display,
  flexWrap,
  zIndex
} from 'styled-system'

export const Box = styled.div.attrs(props => ({
  display: props.inline ? 'inline-block' : 'block'
}))`
  ${display}
  ${space}
  ${width}
  ${height}
  ${fontSize}
  ${position}
  ${maxWidth}
  ${minWidth}
  ${maxHeight}
  ${top}
  ${bottom}
  ${left}
  ${right}
  ${minHeight}
  ${zIndex}
`

export const Flex = styled(Box).attrs(props => ({
  justifyContent: props.justifyContent || (props.center && 'center'),
  alignItems:     props.alignItems || (props.center && 'center'),
  flexDirection:  props.flexDirection || (props.column ? 'column' : 'row'),
  display:        props.inline ? 'inline-flex' : 'flex'
}))`
  ${flex}
  ${alignItems}
  ${justifyContent}
  ${flexDirection}
  ${flexWrap}
 `
